.App {
    height: 100%;
    overflow-y: auto;
}
.tabPageContent {
    min-height: 100%;
}
.searchWrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: .8rem;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0.04rem 0.08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    z-index: 2;
    .searchInput {
        width: 90%;
        height: 70%;
        background: #ededed;
        border-radius: .1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: .28rem;
        color: #666;
        >span {
            margin-left: .1rem;
        }
    }
}
.categogContet {
    margin-top: .84rem;
    margin-left: 2rem;
    overflow: hidden;
}
.categoryWrap {
    height: 100px;
    background: white;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: .3rem;
    position: relative;
    z-index: 0;
    img{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
}
.categoryTitle {
    background: white;
    padding: .2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .28rem;
    color: #2196f3;
    >div {
        width: .4rem;
        height: 1px;
        background: #2196f3;
        margin: 0 .2rem;
    }
}
.subCategory {
    background: white;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 .2rem .2rem .2rem;
    >.subCategoryItem {
        width: 50%;
        >img {
            width: 100%;
            padding: .2rem;
            height: auto;
        }
        .subCategoryItemName {
            font-size: .28rem;
            text-align: center;
            padding: 0.02rem 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}
#tabsWrap {
    position: fixed;
    top: .84rem;
    bottom: 1rem;
    width: 2rem;
    background: white;
    border-right: 1px solid #f2f2f6;
    p{
        width: 100%;
        height: 1rem;
        text-align: center;
        line-height: 1rem;
        &.active{
            border-left: 2px solid #108ee9
        }
    }
}